import axios from '@/utils/uniAxios.js'
export default {
	data(){
		return{
			radio:'0',
			icon:{
				normal:'/static/normal.png',
				active:'/static/activeRadio.png',
			},
			value:'',
			list:['我不想要了','买错或买多了','没有成功使用优惠券','其他'],
			id:'',
			content:'',
			state:''
		}
	},
	onLoad(option) {
		this.id = option.orderId;
		if(option.state){
			this.state = option.state
		}else{
			this.state = ''
		}
		
	},
	methods:{
		onChange(e){
			console.log(e)
			if(e.type==='change'){
				this.radio = e.detail
			}
		},
		save(){
			if(this.radio==='3'){
				if(this.content===''){
					this.$api.msg('请填写原因')
				}
			}else{
				let obj = {
					cancelReason:this.list[this.radio],
					orderId:this.id
				};
				axios.post('/order/cancelOrder',obj).then(res=>{
					if(res.data.code=='200'){
						this.$api.msg('取消成功');
						uni.redirectTo({
							url:'order?state='+this.state
						})
					}
				})
			}
		}
	}
}
